
@import "../../common/stylus/iconfont.css";
.sonHeader {
  position: fixed;
  width: 100%;
  height: 40px;
  text-align: center;
  z-index: 50;
  line-height: 40px;
  background: #028fd6;
}
.sonHeader .model.morning {
  color: #333;
  background-color: #028fd6;
}
.sonHeader .model.night {
  color: #b8b8b8;
  background-color: #555;
}
.sonHeader .model > h3 {
  font-size: 20px;
  color: #fff;
}
.sonHeader .model .back {
  position: absolute;
  left: 10px;
  bottom: 2px;
  height: 40px;
}
.sonHeader .model .back .iconfont {
  position: relative;
  line-height: 40px;
  top: 3px;
  font-size: 22px;
  color: #fff;
}


