
body,
html {
  line-height: 1;
  font-weight: 200;
  font-family: 'PingFang SC', 'STHeitiSC-Light', 'Helvetica-Light', arial, sans-serif;
}
.clearfix {
  display: inline-block;
}
.clearfix:after {
  display: block;
  content: ".";
  height: 0;
  line-height: 0;
  clear: both;
  visibility: hidden;
}
@media (-webkit-min-device-pixel-ratio: 1.5), (min-device-pixel-ratio: 1.5) {
.border-1px::after {
    -webkit-transfor: scaleY(0.7);
    -webkit-transform: scaleY(0.7);
            transform: scaleY(0.7);
}
}
@media (-webkit-min-device-pixel-ratio: 2), (min-device-pixel-ratio: 2) {
.border-1px::after {
    -webkit-transform: scaleY(0.5);
    transform: scaleY(0.5);
}
}
.modal-open {
  position: fixed;
  width: 100%;
}
.model.morning {
  color: #333;
  background-color: #fff;
}
.model.night {
  color: #b8b8b8;
  background-color: #343434;
}
.model .editors {
  position: relative;
  top: 40px;
  width: 92%;
  height: 40px;
  line-height: 40px;
  position: relative;
  padding: 0 10px;
}
.model .editors:after {
  display: block;
  position: absolute;
  width: 100%;
  left: 0;
  bottom: 0;
  border-top: 1px solid rgba(7,17,27,0.1);
  content: ' ';
}
.model .editors .editor {
  font-size: 17px;
  color: #666;
}
.model .editors .avatar {
  display: inline-block;
}
.model .editors .avatar img {
  margin-left: 10px;
  border-radius: 50%;
}
.model .editors .arrow_right {
  position: absolute;
  right: 10px;
  padding: 0 10px;
}
.model .themeNewList {
  position: relative;
  top: 40px;
  width: 100%;
  height: 100%;
}
.model .themeNewList.morning {
  color: #333;
  background-color: #fff;
}
.model .themeNewList.night {
  color: #b8b8b8;
  background-color: #343434;
}
.model .themeNewList .new {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  position: relative;
  left: -15px;
  padding: 20px 0 20px 10px;
  position: relative;
}
.model .themeNewList .new:after {
  display: block;
  position: absolute;
  width: 100%;
  left: 0;
  bottom: 0;
  border-top: 1px solid rgba(7,17,27,0.1);
  content: ' ';
}
.model .themeNewList .new .title {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  margin-right: 10px;
  line-height: 20px;
}
.model .themeNewList .new .avatar {
  -webkit-box-flex: 0;
      -ms-flex: 0 0 70px;
          flex: 0 0 70px;
  width: 70px;
  height: 55px;
}
.model .themeNewList .new .avatar img {
  width: 70px;
  height: 55px;
}
.model .themeNewList .new .avatar img img[lazy=loading] {
  height: 55px;
  width: 70px;
  margin: auto;
}


