
body,
html {
  line-height: 1;
  font-weight: 200;
  font-family: 'PingFang SC', 'STHeitiSC-Light', 'Helvetica-Light', arial, sans-serif;
}
.clearfix {
  display: inline-block;
}
.clearfix:after {
  display: block;
  content: ".";
  height: 0;
  line-height: 0;
  clear: both;
  visibility: hidden;
}
@media (-webkit-min-device-pixel-ratio: 1.5), (min-device-pixel-ratio: 1.5) {
.border-1px::after {
    -webkit-transfor: scaleY(0.7);
    -webkit-transform: scaleY(0.7);
            transform: scaleY(0.7);
}
}
@media (-webkit-min-device-pixel-ratio: 2), (min-device-pixel-ratio: 2) {
.border-1px::after {
    -webkit-transform: scaleY(0.5);
    transform: scaleY(0.5);
}
}
@import "../../common/stylus/iconfont.css";
.bottomMenu {
  position: fixed;
  bottom: -1px;
  height: 44px;
  width: 100%;
  background-color: #fff;
}
.bottomMenu .model {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.bottomMenu .model.morning {
  color: #333;
  background-color: #fff;
  border-top: 1px solid #f5f5f5;
}
.bottomMenu .model.night {
  color: #b8b8b8;
  background-color: #343434;
}
.bottomMenu .model .menu {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  line-height: 39px;
  font-size: 30px;
  text-align: center;
  margin-bottom: 5px;
}
.bottomMenu .model .menu.isThumbUp {
  color: #00bfff;
}
.bottomMenu .model .menu .iconfont {
  font-size: 25px;
}
.bottomMenu .model .menu img {
  position: relative;
  bottom: 3px;
}
.bottomMenu .model .menu .extra {
  position: relative;
  bottom: 15px;
  font-size: 15px;
}
.bottomMenu .model .share {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 380px;
  background: #f5f5f5;
  z-index: 50;
  -webkit-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);
}
.bottomMenu .model .share.fold-enter-active,
.bottomMenu .model .share.fold-leave-active {
  transition: all 0.5s;
}
.bottomMenu .model .share.fold-enter,
.bottomMenu .model .share.fold-leave-active {
  -webkit-transform: translate3d(0, 100%, 0);
          transform: translate3d(0, 100%, 0);
}
.bottomMenu .model .share.morning {
  color: #333;
  background-color: #e9e9e9;
  border-top: 1px solid #f5f5f5;
}
.bottomMenu .model .share.night {
  color: #b8b8b8;
  background-color: #343434;
}
.bottomMenu .model .share .title {
  width: 100%;
  height: 25px;
  text-align: center;
  font-size: 17px;
  padding-top: 10px;
}
.bottomMenu .model .share .shareMenus {
  position: relative;
  top: 5px;
  width: 100%;
  font-size: 0;
}
.bottomMenu .model .share .shareMenus .shareMenu {
  display: inline-block;
  padding-top: 15px;
  z-index: 50;
  width: 25%;
  margin-bottom: 30px;
  text-align: center;
}
.bottomMenu .model .share .shareMenus .shareMenu i {
  font-size: 25px;
  padding: 10px;
  border-radius: 50%;
  color: #fff;
}
.bottomMenu .model .share .shareMenus .shareMenu i.icon-weixinhaoyou {
  background: #64c654;
}
.bottomMenu .model .share .shareMenus .shareMenu i.icon-weixinpengyouquan {
  background: #75cd37;
}
.bottomMenu .model .share .shareMenus .shareMenu i.icon-QQ {
  background: #286bc4;
}
.bottomMenu .model .share .shareMenus .shareMenu i.icon-weibo {
  background: #ec4226;
}
.bottomMenu .model .share .shareMenus .shareMenu i.icon-fuzhi {
  background: #dd472c;
}
.bottomMenu .model .share .shareMenus .shareMenu i.icon-youdaoyunbiji {
  background: #38a3ea;
}
.bottomMenu .model .share .shareMenus .shareMenu i.icon-yinxiangbiji {
  background: #5db346;
}
.bottomMenu .model .share .shareMenus .shareMenu i.icon-tengxunweibo {
  background: #3ea1d9;
}
.bottomMenu .model .share .shareMenus .shareMenu i.icon-xinxi {
  background: #4fe54e;
}
.bottomMenu .model .share .shareMenus .shareMenu i.icon-instapaper {
  background: #6c6c6c;
}
.bottomMenu .model .share .shareMenus .shareMenu .name {
  display: block;
  position: relative;
  top: 20px;
  font-size: 18px;
  text-align: center;
}
.bottomMenu .model .share .button {
  position: relative;
  height: 40px;
  width: 80%;
  bottom: 5px;
  margin: 15px auto;
  background: #fff;
  text-align: center;
  line-height: 40px;
}
.bottomMenu .model .share .button.morning {
  color: #333;
  background-color: #fff;
}
.bottomMenu .model .share .button.night {
  color: #b8b8b8;
  background-color: #555;
}
.bottomMenu .model .mask {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(7,17,27,0.6);
  z-index: 40;
  opacity: 1;
}
.bottomMenu .model .mask.fade-enter-active,
.bottomMenu .model .mask.fade-leave-active {
  transition: all 0.5s;
}
.bottomMenu .model .mask.fade-enter,
.bottomMenu .model .mask.fade-leave-active {
  opacity: 0;
}
.modal-open {
  position: fixed;
  width: 100%;
}


