
body,
html {
  line-height: 1;
  font-weight: 200;
  font-family: 'PingFang SC', 'STHeitiSC-Light', 'Helvetica-Light', arial, sans-serif;
}
.clearfix {
  display: inline-block;
}
.clearfix:after {
  display: block;
  content: ".";
  height: 0;
  line-height: 0;
  clear: both;
  visibility: hidden;
}
@media (-webkit-min-device-pixel-ratio: 1.5), (min-device-pixel-ratio: 1.5) {
.border-1px::after {
    -webkit-transfor: scaleY(0.7);
    -webkit-transform: scaleY(0.7);
            transform: scaleY(0.7);
}
}
@media (-webkit-min-device-pixel-ratio: 2), (min-device-pixel-ratio: 2) {
.border-1px::after {
    -webkit-transform: scaleY(0.5);
    transform: scaleY(0.5);
}
}
.comments {
  width: 100%;
  height: 100%;
}
.comments .commentsArea {
  position: absolute;
  top: 40px;
  height: 100%;
  width: 100%;
}
.comments .commentsArea.morning {
  background-color: #fff;
}
.comments .commentsArea.night {
  background-color: #555;
}
.comments .commentsArea .longComments,
.comments .commentsArea .shortComments {
  width: 100%;
}
.comments .commentsArea .longComments.morning,
.comments .commentsArea .shortComments.morning {
  color: #333;
  background-color: #fff;
}
.comments .commentsArea .longComments.night,
.comments .commentsArea .shortComments.night {
