  background-color: #555;
}
.comments .commentsArea .longComments .longCommentsTitle,
.comments .commentsArea .shortComments .longCommentsTitle,
.comments .commentsArea .longComments .shortCommentsTitle,
.comments .commentsArea .shortComments .shortCommentsTitle {
  height: 40px;
  line-height: 40px;
  padding-left: 10px;
  font-weight: 700;
  color: #07111b;
  position: relative;
}
.comments .commentsArea .longComments .longCommentsTitle:after,
.comments .commentsArea .shortComments .longCommentsTitle:after,
.comments .commentsArea .longComments .shortCommentsTitle:after,
.comments .commentsArea .shortComments .shortCommentsTitle:after {
  display: block;
  position: absolute;
  width: 100%;
  left: 0;
  bottom: 0;
  border-top: 1px solid rgba(7,17,27,0.1);
  content: ' ';
}
.comments .commentsArea .longComments .longCommentsTitle .thumb,
.comments .commentsArea .shortComments .longCommentsTitle .thumb,
.comments .commentsArea .longComments .shortCommentsTitle .thumb,
.comments .commentsArea .shortComments .shortCommentsTitle .thumb {
  position: absolute;
  top: 8px;
  right: 10px;
