  height: 20px;
}
.comments .commentsArea .longComments .longComment,
.comments .commentsArea .shortComments .longComment,
.comments .commentsArea .longComments .shortComment,
.comments .commentsArea .shortComments .shortComment {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 94%;
  padding: 10px 10px 0 10px;
  position: relative;
}
.comments .commentsArea .longComments .longComment:after,
.comments .commentsArea .shortComments .longComment:after,
.comments .commentsArea .longComments .shortComment:after,
.comments .commentsArea .shortComments .shortComment:after {
  display: block;
  position: absolute;
  width: 100%;
  left: 0;
  bottom: 0;
  border-top: 1px solid rgba(7,17,27,0.1);
  content: ' ';
}
.comments .commentsArea .longComments .longComment .avatar,
.comments .commentsArea .shortComments .longComment .avatar,
.comments .commentsArea .longComments .shortComment .avatar,
.comments .commentsArea .shortComments .shortComment .avatar {
  -webkit-box-flex: 0;
      -ms-flex: 0 0 30px;
          flex: 0 0 30px;
  width: 30px;
  height: 30px;
}
.comments .commentsArea .longComments .longComment .avatar img,
.comments .commentsArea .shortComments .longComment .avatar img,
.comments .commentsArea .longComments .shortComment .avatar img,
.comments .commentsArea .shortComments .shortComment .avatar img {
