}
.comments .commentsArea .longComments .longComment .content,
.comments .commentsArea .shortComments .longComment .content,
.comments .commentsArea .longComments .shortComment .content,
.comments .commentsArea .shortComments .shortComment .content {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  position: relative;
  margin-left: 10px;
  top: -10px;
}
.comments .commentsArea .longComments .longComment .content .name,
.comments .commentsArea .shortComments .longComment .content .name,
.comments .commentsArea .longComments .shortComment .content .name,
.comments .commentsArea .shortComments .shortComment .content .name {
