}
.comments .commentsArea .longComments .longComment .content .likes,
.comments .commentsArea .shortComments .longComment .content .likes,
.comments .commentsArea .longComments .shortComment .content .likes,
.comments .commentsArea .shortComments .shortComment .content .likes {
