  height: 20px;
  right: 5px;
}
.comments .commentsArea .longComments .longComment .content .likes img,
.comments .commentsArea .shortComments .longComment .content .likes img,
.comments .commentsArea .longComments .shortComment .content .likes img,
.comments .commentsArea .shortComments .shortComment .content .likes img {
  position: absolute;
  right: 20px;
  top: -25px;
}
.comments .commentsArea .longComments .longComment .content .comment,
.comments .commentsArea .shortComments .longComment .content .comment,
.comments .commentsArea .longComments .shortComment .content .comment,
.comments .commentsArea .shortComments .shortComment .content .comment {
  font-size: 18px;
}


