
.header {
  position: fixed;
  top: 0;
  left: 0;
  font-size: 0;
  width: 100%;
  height: 40px;
  z-index: 5;
  text-align: center;
}
.header .model.morning {
  color: #333;
  background-color: #028fd6;
}
.header .model.night {
  color: #b8b8b8;
  background-color: #555;
}
.header .model > h2 {
  line-height: 40px;
  font-size: 22px;
  color: #fff;
}
.header .model .navigation {
  position: absolute;
  top: 10px;
  width: 20px;
  height: 20px;
  font-size: 0;
  left: 10px;
  padding: 5px;
  background-image: url("navigation.png");
  background-size: 20px 20px;
  background-repeat: no-repeat;
}


