
.swipe {
  position: relative;
  top: 40px;
  height: 200px;
  width: 100%;
}
.swipe img {
  width: 100%;
  height: 200px;
  z-index: 1;
}
.swipe .title {
  position: absolute;
  top: 150px;
  padding: 0 10px;
  z-index: 2;
  font-size: 20px;
  color: #fff;
  height: 50px;
}


