
body,
html {
  line-height: 1;
  font-weight: 200;
  font-family: 'PingFang SC', 'STHeitiSC-Light', 'Helvetica-Light', arial, sans-serif;
}
.clearfix {
  display: inline-block;
}
.clearfix:after {
  display: block;
  content: ".";
  height: 0;
  line-height: 0;
  clear: both;
  visibility: hidden;
}
@media (-webkit-min-device-pixel-ratio: 1.5), (min-device-pixel-ratio: 1.5) {
.border-1px::after {
    -webkit-transfor: scaleY(0.7);
    -webkit-transform: scaleY(0.7);
            transform: scaleY(0.7);
}
}
@media (-webkit-min-device-pixel-ratio: 2), (min-device-pixel-ratio: 2) {
.border-1px::after {
    -webkit-transform: scaleY(0.5);
    transform: scaleY(0.5);
}
}
.collect {
  position: fixed;
  width: 100%;
  height: 100%;
  z-index: 100;
  background: #fff;
  -webkit-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);
}
.collect.fold-enter-active,
.collect.fold-leave-active {
  transition: all 0.5s;
}
.collect.fold-enter,
.collect.fold-leave-active {
  -webkit-transform: translate3d(100%, 0, 0);
          transform: translate3d(100%, 0, 0);
}
.collect .model {
  position: absolute;
  width: 100%;
  height: 100%;
}
.collect .model.morning {
  color: #333;
  background-color: #fff;
}
.collect .model.night {
  color: #b8b8b8;
  background-color: #343434;
}
.collect .model .collectNewList {
  position: absolute;
  top: 40px;
  width: 100%;
  z-index: 30;
}
.collect .model .collectNewList .new {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  position: relative;
  left: -15px;
  padding: 15px 0 15px 10px;
  position: relative;
}
.collect .model .collectNewList .new:after {
  display: block;
  position: absolute;
  width: 100%;
  left: 0;
  bottom: 0;
  border-top: 1px solid rgba(7,17,27,0.1);
  content: ' ';
}
.collect .model .collectNewList .new .title {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  margin-right: 10px;
  line-height: 20px;
}
.collect .model .collectNewList .new .avatar {
  -webkit-box-flex: 0;
      -ms-flex: 0 0 70px;
          flex: 0 0 70px;
  width: 70px;
  height: 55px;
}
.collect .model .collectNewList .new .avatar img {
  width: 70px;
  height: 55px;
}


